                            <div slot="desc"><p>通过指定<code>dealy</code>延迟显示</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-tooltip</span> <span class="hljs-attr">content</span>=<span class="hljs-string">"显示消息"</span> <span class="hljs-attr">:delay</span>=<span class="hljs-string">"1000"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-button</span>&gt;</span>延迟1s显示<span class="hljs-tag">&lt;/<span class="hljs-name">xl-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">xl-tooltip</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="zong-shi-xian-shi"><a class="header-anchor" href="#zong-shi-xian-shi" aria-hidden="true">¶</a> 总是显示</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;div class=\&quot;demo-tooltip\&quot;&gt;\n    &lt;xl-checkbox v-model=\&quot;always\&quot; label=\&quot;是否总显示\&quot;&gt;&lt;/xl-checkbox&gt;\n&lt;/div&gt;\n&lt;div class=\&quot;demo-tooltip\&quot;&gt;\n    &lt;xl-checkbox v-model=\&quot;disabled\&quot; label=\&quot;是否总不显示\&quot;&gt;&lt;/xl-checkbox&gt;\n&lt;/div&gt;\n&lt;xl-tooltip content=\&quot;显示消息\&quot; :always=\&quot;always\&quot; :disabled=\&quot;disabled\&quot;&gt;\n    &lt;xl-button&gt;{{disabled?\&quot;禁止提示框\&quot;:always?\&quot;总是显示提示框\&quot;:\&quot;鼠标悬浮显示\&quot;}}&lt;/xl-button&gt;\n&lt;/xl-tooltip&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n    export default{\n        data(){\n            return{\n                disabled:false,\n                always: false\n            }\n        }\n    }\n&quot;,&quot;style&quot;:&quot;\n    .demo-tooltip{\n        margin-bottom:2em;\n    }\n&quot;}">
                            <div><div class="demo-tooltip">
    <xl-checkbox v-model="always" label="是否总显示"></xl-checkbox>
</div>
<div class="demo-tooltip">
    <xl-checkbox v-model="disabled" label="是否总不显示"></xl-checkbox>
</div>
<xl-tooltip content="显示消息" :always="always" :disabled="disabled">
    <xl-button>{{disabled?"禁止提示框":always?"总是显示提示框":"鼠标悬浮显示"}}</xl-button>
</xl-tooltip>


</div>
                            <div slot="desc"><p>通过指定<code>always</code></p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-tooltip"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"always"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"是否总显示"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-tooltip"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"disabled"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"是否总不显示"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">xl-tooltip</span> <span class="hljs-attr">content</span>=<span class="hljs-string">"显示消息"</span> <span class="hljs-attr">:always</span>=<span class="hljs-string">"always"</span> <span class="hljs-attr">:disabled</span>=<span class="hljs-string">"disabled"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-button</span>&gt;</span>{{disabled?"禁止提示框":always?"总是显示提示框":"鼠标悬浮显示"}}<span class="hljs-tag">&lt;/<span class="hljs-name">xl-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">xl-tooltip</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span>{
        data(){
            <span class="hljs-keyword">return</span>{
                <span class="hljs-attr">disabled</span>:<span class="hljs-literal">false</span>,
                <span class="hljs-attr">always</span>: <span class="hljs-literal">false</span>
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">style</span>&gt;</span><span class="css">
    <span class="hljs-selector-class">.demo-tooltip</span>{
        <span class="hljs-attribute">margin-bottom</span>:<span class="hljs-number">2em</span>;
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">style</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="tooltip-shu-xing"><a class="header-anchor" href="#tooltip-shu-xing" aria-hidden="true">¶</a> Tooltip 属性</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>transfer</td>
<td>是否将弹出框置于body中</td>
<td>boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>placement</td>
<td>弹出框相对显示框的位置</td>
<td>string</td>
<td>top/top-start/top-end/bottom/bottom-start/bottom-end/left/left-start/left-end/right/right-start/right-end</td>
<td>bottom</td>
</tr>
<tr>
<td>width</td>
<td>弹出框宽,通过设置{min:10,max:20}来设置最小宽度和最大宽度</td>
<td>string/number/object</td>
<td>-</td>
<td>{min: 150,max: 300}</td>
</tr>
<tr>
<td>content</td>
<td>内容</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>padding</td>
<td>自定义边距</td>
<td>string</td>
<td>-</td>
<td>'0.5em,1em'</td>
</tr>
<tr>
<td>dangerous-html</td>
<td>允许嵌入HTML</td>
<td>boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>delay</td>
<td>延迟显示</td>
<td>number(ms)</td>
<td>-</td>
<td>0</td>
</tr>
<tr>
<td>disabled</td>
<td>是否总不显示</td>
<td>boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>always</td>
<td>是否总是显示(<code class="code_inline">disabled=false</code>下可用)</td>
<td>boolean</td>
<td>-</td>
<td>false</td>
</tr>
</tbody>
</table>
<h2 id="tooltip-slots"><a class="header-anchor" href="#tooltip-slots" aria-hidden="true">¶</a> Tooltip slots</h2>
<table class="table"><thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>default</td>
<td>插入显示框</td>
</tr>
</tbody>
</table>
</section></template>
<style>
    .demo-tooltip{
        margin-bottom:2em;
    }
</style>
<script>
    export default{
        data(){
            return{
                disabled:false,
                always: false
            }
        }
    }
</script>


